import React, { Component } from 'react';
import { Table, Row, Col } from 'react-bootstrap';
import '../../../shared/CSS/FormStyles.css';
import styles from './Polls.module.css';
import { Button } from 'reactstrap';
import Select from './Charts/ChartSelect';
import * as services from "../../../services/services";

class Polls extends Component {

    state = {
        showForm: false,
        showResult: false,
        id: "",
        pollTitle: "",
        initiatedBy: "",
        priority: "",
        status: "",
        vote: "",
        activePoll: {},
        data: []
    }

    componentDidMount() {
        this.getPolls();
    }

    async getPolls() {
        await services.getService("polls").then((res) => {
            console.log(res.data);
            this.setState({ data: res.data.Polls });
        });
    }

    submitVoteHandler = async () => {
        let data = this.state.activePoll;

        data.vote = this.state.vote;
        data.type = "response"

        // console.log(this.state.vote);
        delete data._id;
        services.postService("polls", data).then((res) => {
            console.log(res.data)
            this.getPolls();
            this.setState({
                showForm: !this.state.showForm
            });
        })
    };

    giveVoteHandler = (id) => {
        const newData = this.state.data.filter((obj) => obj._id === id)[0];
        this.setState({
            showForm: !this.state.showForm
        })
        this.setState({
            activePoll: newData,
        });
    };

    showForm = () => {
        return (
            <div>

                <div className="row asy-main-row">
                    <Col>
                        <h5 className="asy-main-page-heading"> Polls</h5>
                    </Col>
                </div>


                <div className="form-container ">
                    <div className="asy-card-primary-design" >
                        <form>
                            <Row>
                                <div id="LabelArea" className="form-group ">
                                    <label id="FirstLabelId" for="inputdegree" className="col-form-label ml-4">Poll Title </label>
                                    <div className="SecondLabel">
                                        <label id="SecondLabelId" for="inputdegree" className="col-form-label ">
                                            {
                                                this.state.activePoll.pollTitle
                                            }
                                        </label>
                                    </div>
                                </div>

                                <div id="LabelArea" className="form-group ">
                                    <label id="FirstLabelId" for="inputdegree" className="col-form-label ml-4">Poll Priority  </label>
                                    <div className="SecondLabel">
                                        <label id="SecondLabelId" for="inputdegree" className="col-form-label">
                                            {
                                                this.state.activePoll.priority
                                            }
                                        </label>
                                    </div>
                                </div>

                                <div id="LabelArea" className="form-group ">
                                    <label id="FirstLabelId" for="inputdegree" className="col-form-label ml-4">Initiated By </label>
                                    <div className="SecondLabel2">
                                        <label id="SecondLabelId" for="inputdegree" className="col-form-label">
                                            {
                                                this.state.activePoll.initiatedBy
                                            }
                                        </label>
                                    </div>
                                </div>
                            </Row>

                            <hr className="hr-line-2" />
                            <Row>
                                <div className="questionFont">
                                    <label for="inputdegree" className="col-form-label pt-4 ml-4"> {this.state.activePoll.question} </label>
                                </div>
                            </Row>

                            <Row>
                                <div class="form-check form-check-inline">
                                    {
                                        this.state.activePoll.options.map((data) => (
                                            <div key={data.id}>
                                                <div id="submitid">
                                                    <div className={styles.radio_btns}>
                                                        <label>
                                                            <input type="radio" name="radio" value={data.value} onChange={(e) => { this.setState({ vote: e.target.value }) }} />
                                                            {data.value}
                                                            <div class={styles.check}></div>
                                                        </label></div>
                                                </div>
                                            </div>
                                        ))
                                    }

                                </div>

                            </Row>

                            <div className=" text-center my-2">
                                <Button className=" asy-secondary-submit-button" onClick={this.submitVoteHandler}>
                                    Submit Vote
                                </Button>
                                <Button className="asy-secondary-cancle-button" onClick={() => this.setState({ showForm: !this.state.showForm })}>
                                    Cancel
                                </Button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }

    showResult = () => {
        return (
            <div>

                <div className="row asy-main-row">
                    <Col>
                        <h5 className="asy-main-page-heading"> Polls</h5>
                    </Col>


                    <Col className="d-flex flex-row-reverse">
                        <Button className="add-button-flip" onClick={() => this.setState({ showResult: !this.state.showResult })}>
                            Back
                        </Button>
                    </Col>
                </div>


                <div className="text-center">
                    <div className='CardArea' >
                        <div className='card polls mr-2'>
                            <p className="asy-card-page-heading  text-center mt-3 font-weight-bold">Section Wise Analysis</p>
                            <hr className="hr-line-2" />
                            <div className="card-body polls">
                                <Select />
                            </div>
                        </div>

                        <div className='card polls'>
                            <p className="asy-card-page-heading text-center mt-3 font-weight-bold">Complete Analysis</p>
                            <hr className="hr-line-2" />
                            <div className="card-body polls">
                                <Select />
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        );
    }

    render() {
        return (
            <div>
                {
                    this.state.showForm ? this.showForm() :
                        <div>
                            {
                                this.state.showResult ? this.showResult() :

                                    <div>

                                        <div className="row asy-main-row">
                                            <Col>
                                                <h5 className="asy-main-page-heading"> Polls</h5>
                                            </Col>
                                        </div>


                                        <div className="form-container">
                                            <form>
                                                <div className="table-sm asy-mainBoxBorder asy-Tablestriped table-responsive">
                                                    <Table className="asy-Table">
                                                        <thead>
                                                            <tr className="asy-TableHeading">
                                                                <th className="asy-th">Poll Title</th>
                                                                <th className="asy-th">Initiated by</th>
                                                                <th className="asy-th">Priority</th>
                                                                <th className="asy-th">Status</th>
                                                                <th className="columnwidth asy-th"></th>
                                                            </tr>
                                                        </thead>

                                                        <tbody>
                                                            {
                                                                this.state.data.map((data) => (
                                                                    <tr className="asy-TableDataRow" key={data._id}>
                                                                        <td>
                                                                            {data.pollTitle}
                                                                        </td>
                                                                        <td>
                                                                            {data.initiatedBy}
                                                                        </td>
                                                                        <td>
                                                                            {data.priority}
                                                                        </td>
                                                                        <td>
                                                                            {data.status}
                                                                        </td>
                                                                        <td id="btn-align">

                                                                            <Button className="asy-secondary-submit-button m-0 mr-1" onClick={() => this.setState({ showResult: !this.state.showResult })}>
                                                                                View Result
                                                                            </Button>
                                                                            <Button className="asy-secondary-submit-button m-0" onClick={this.giveVoteHandler.bind(this, data._id)}>
                                                                                Give Vote
                                                                            </Button>
                                                                        </td>
                                                                    </tr>
                                                                ))
                                                            }
                                                        </tbody>

                                                    </Table>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                            }
                        </div>
                }
            </div>
        );
    }
}


export default Polls;