import React, { useState } from 'react';
import styles from './task.module.css';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import _ from "lodash";
import * as services from "../../../services/services";
import { Col } from 'react-bootstrap';
import moment from "moment";
import Avatar from 'react-avatar';

const Task = () => {

	const [state, setState] = useState({})
	var Data = [];

	React.useEffect(() => {
		getUsersData();
	}, []);

	async function getUsersData() {
		services.getService("tasks").then((res) => {
			Data = res.data.Task;
			const Status1 = Data.filter(data => data.status === "TO DO");
			const Status2 = Data.filter(data => data.status === "IN DEVELOPMENT");
			const Status3 = Data.filter(data => data.status === "TECHNICAL REVIEW");
			const Status4 = Data.filter(data => data.status === "DEPLOYMENT");

			setState({
				"TO DO": {
					title: "To Do",
					status: "TO DO",
					items: Status1
				},
				"IN DEVELOPMENT": {
					title: "In Development",
					status: "IN DEVELOPMENT",
					items: Status2
				},
				"TECHNICAL REVIEW": {
					title: "Technical Review",
					status: "TECHNICAL REVIEW",
					items: Status3
				},
				"DEPLOYMENT": {
					title: "Deployment",
					status: "DEPLOYMENT",
					items: Status4
				},
			})
		});
	}

	const handleDragEnd = ({ destination, source }) => {
		if (!destination) {
			return
		}
		if (destination.index === source.index && destination.droppableId === source.droppableId) {
			return
		}
		const itemCopy = { ...state[source.droppableId].items[source.index] }
		setState(prev => {
			prev = { ...prev }
			prev[source.droppableId].items.splice(source.index, 1)
			prev[destination.droppableId].items.splice(destination.index, 0, itemCopy)
			return prev
		})

		const data = { status: destination.droppableId }
		console.log('itemcopy', itemCopy._id);

		services.patchService("tasks", itemCopy._id, data).then((res) => {
			console.log(res.data);
			getUsersData();
		});
	}

	return (
		<div className={[styles.TaskPageContent, "asy-main-jumbotron"].join(' ')}>
			<div className="row asy-main-row">
				<Col>
					<h5 className="asy-main-page-heading"> My Task </h5>
				</Col>
			</div>
			<div className="text-center center" >
				<div id={[styles.row]} className="row row-cols-1 row-cols-md-4">
					<DragDropContext onDragEnd={handleDragEnd} >
						{_.map(state, (data, key) => {
							return (
								<div className={[styles.col]}>
									<div className={[styles.card, "h-100"].join(' ')}>
										<div className={styles['card-body']}>
											<div align="left" className="col-sm">
												<h6>{data.title}</h6>
											</div>
											<div key={key} className={styles['card-body']}>
												<Droppable droppableId={key}>
													{(provided) => {
														return (
															<div ref={provided.innerRef}
																{...provided.droppableProps}
																className={styles['card-body']}>
																{data.items.map((el, index) => {
																	return (
																		<Draggable key={el._id} index={index} draggableId={el._id}>
																			{(provided) => {
																				return (
																					<div className={[styles.SubBox]}
																						ref={provided.innerRef}
																						{...provided.draggableProps}
																						{...provided.dragHandleProps} >
																						<div align="left">
																							<div className={styles.taskLabel}> Title : <div className={[styles.taskLabelValues, "d-inline-block"].join(' ')}>{el.title} </div> </div>
																							<div className={styles.taskLabel}> Due On : <div className={[styles.taskLabelValues, "d-inline-block"].join(' ')}>{moment(el.dueDate).format("D MMM YYYY")} </div>  </div>
																							<div className={styles.taskLabel}> Priority : <div className={[styles.taskLabelValues, "d-inline-block"].join(' ')}>{el.priority} </div></div>

																						</div>
																						<footer className="d-flex">
																							<div align="left" className="mr-auto" >
																								<img className={styles.GripsLines} src="https://img.icons8.com/windows/32/000000/grip-lines.png" />
																							</div>
																							<div align="right">
																								<Avatar color="rgb(122, 94, 100)" name="Rahul Patil" round="15px" size="20" />
																							</div>
																						</footer>
																					</div>
																				)
																			}
																			}
																		</Draggable>
																	)
																})}
																{provided.placeholder}
															</div>
														)
													}}
												</Droppable>
											</div>
										</div>
									</div>
								</div>
							)
						})}
					</DragDropContext>
				</div>
			</div>
		</div>
	)
}
export default Task;
